}


/* admin_news_and_events_page_main */

#admin_news_and_events_page_main{
	width: 100%;
	height: 110vh;
	background-color: var(--third-color);
	color: var(--fourth-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	padding-bottom: 5%;
}

